<template>
  <v-card flat>
    <v-card-text>
      <v-row>
        <v-spacer></v-spacer>
        <v-col cols="4" sm="2" md="2" lg="1">
          <v-btn
            color="success"
            @click="addAction"
            ></v-btn
          >
        </v-col>
      </v-row>
      <v-row>
        <v-col>
          <v-data-table
            :headers="headers"
            :items="announcementList"
            :items-per-page="5"
            class="elevation-1"
          >
            <template v-slot:[`item.index`]="{ index }">
              {{ index + 1 }}
            </template>
            <template v-slot:[`item.title`]="{ item }">
              <span
                class="d-inline-block text-truncate"
                style="max-width: 150px"
                >{{ item.title }}</span
              >
            </template>
            <template v-slot:[`item.content`]="{ item }">
              <span
                class="d-inline-block text-truncate"
                style="max-width: 600px"
                >{{ item.content }}</span
              >
            </template>
            <template v-slot:[`item.createdAt`]="{ item }">
              {{$moment(item.createdAt).format('YYYY-MM-DD HH:mm:ss')}}
            </template>
            <template v-slot:[`item.actions`]="{ item }">
              <v-btn
                color="warning"
                @click="editAction(item)"
              >
                
              </v-btn>
              <v-btn
                color="error"
                @click="showDeleteDialog(item)"
              >
                
              </v-btn>
            </template>
          </v-data-table>
        </v-col>
        
      </v-row>
                    <v-btn @click="($router.go(-1))" color="blue" dark class="mt-6" width="100%">
                <v-icon>
                  mdi-arrow-u-left-bottom
                </v-icon>
              </v-btn>
    </v-card-text>
  </v-card>
</template>

<script>
export default {
  name: "TaBle",
  components: {},
  data() {
    return {
      headers: [
        { text: "#", value: "index" },
        { text: "", value: "title" },
        { text: "", value: "content" },
        { text: "", value: "createdAt" },
        { text: "", value: "browsing" },
        { text: "", value: "actions", sortable: false },
      ],
      announcementList: [
        {
            title:"1",
            content:"1",
            createdAt:"2018-09-28T10:55:51.603Z",
            browsing:"100",
            uuid:"1",
        },
        {
            title:"2",
            content:"2",
            createdAt:"2018-09-28T10:55:51.603Z",
            browsing:"200",
            uuid:"2",
        },
        {
            title:"3",
            content:"3",
            createdAt:"2018-09-28T10:55:51.603Z",
            browsing:"300",
            uuid:"3",
        },
        {
            title:"4",
            content:"4",
            createdAt:"2018-09-28T10:55:51.603Z",
            browsing:"400",
            uuid:"4",
        },
        {
            title:"5",
            content:"5",
            createdAt:"2018-09-28T10:55:51.603Z",
            browsing:"500",
            uuid:"5",
        },

        
      ],
    };
  },
  methods: {
    addAction() {
      this.$router.push(`table/add`);
    },
    editAction(data) {
      this.$router.push(`table/edit?uuid=${data.uuid}`);
    },
    async showDeleteDialog(data) {
      var check = confirm("");
      if (check == true) {
        for(let i in this.announcementList){
            if(this.announcementList[i].uuid ==data.uuid){
                this.announcementList.splice(i,1)
                break
            }
        }
      }
    },
  },

};
</script>